import san from "@/assets/san.png";
import chainIcon from "@/assets/chainIcon.png";
export default {
  data() {
    return {
      showPositionValue: false, //提示信息
      toastContent: "",
      topTitle: "添加提币地址",
      san: san,
      chainIcon: chainIcon,
      list: [],
      listVal: "",
      note: "",
      form: {
        coin_type: "",
        chain: "",
        alias_name: "0",
        setting: false,
        address: "",
        coin_status:"",
        remark: "",
        memo: ""
      },
      scan:null
    };
  },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    this.getCoin();
  },
  methods: {
    // /获取币种币链
    getCoin() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/get-coin",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
          that.form.coin_type = that.list[0].coin_type;
          that.getChainCoin(that.form.coin_type);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getChainCoin(val) {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          coin_type: val
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/get-chain-coin",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.form.coin_status=response.data[0].coin_status;
          that.form.alias_name = response.data[0].alias_name;
          that.form.chain = response.data[0].chain;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 币链接信息介绍
    getInfo() {
      this.$router.push("/chainCoinInfo");
    },
    // 保存
    getSubmt() {
      var that = this,
        formData,
        setVal;
      if (that.form.setting == false) {
        setVal = 2;
      } else {
        setVal = 1;
      }
      if (that.form.address == "") {
        that.showPositionValue = true;
        that.toastContent = "请您输入地址";
      } else if (that.form.remark == "") {
        that.showPositionValue = true;
        that.toastContent = "请您输入备注";
      } else {
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          coin_type: that.form.coin_type,
          chain: that.form.chain,
          address: that.form.address,
          setting: setVal,
          remark: that.form.remark
        };
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/personal/add-address",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 0) {
               that.showPositionValue = true;
              that.toastContent = response.errmsg;
              setTimeout(function(){ that.$router.push("/wallet/address");},500)
            } else {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    getQC(){
    }
  }
};



